<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'

  // Routes
  import routesConfig, { getPage, getAppBarContent } from './routes'

  // Stores
  import { snackList, meData } from './stores/main'
  import { urlData } from './stores/router'

  // Components
  import FetchProgress from './components/FetchProgress.svelte'
  import Input from './components/Input.svelte'
  import SnackMessage from './components/SnackMessage.svelte'
  import ModalContainer from './components/Modal.svelte'
  import DefaultBar from './routes/_DefaultBar.svelte'

  // API
  import { me } from './api/user'
  import { getUserAsync } from './helpers/auth'

  export let mainRouter
  routesConfig(mainRouter)

  let bar
  let component
  let searchTitle
  let modal
  let hideSearch

  $: {
    const ab = getAppBarContent($urlData.segment)
    bar = ab.bar || null
    component = ab.component || null
    hideSearch = ab.hideSearch || false
    searchTitle = ab.searchTitle || ''
    modal = ab.modal || null
  }

  onMount(() => {
    window.clientSocket.on('notification', data => {
      modalDataList.add(data)
    })
    window.clientSocket.on('reload', (data, pid) => {
      const { project } = $urlData.query

      if (pid && project && pid !== project) return

      console.log('%csocket-event:', 'color:#16f; text-transform:uppercase;')
      console.log(data)
      window.dispatchEvent(new CustomEvent(data))
    })
    getUserAsync()
      .then(user => {
        window.clientSocket.emit('join_room', user.uid)
        return Promise.all([user.getIdToken(), me()])
      })
      .then(([token, me]) => {
        setInitLoading(false)
        meData.set(me)
        if (me.master) {
          window.clientSocket.emit('join_room', 'master')
        }
        if ($urlData.segment === '' || $urlData.segment === 'auth') {
          $urlData.segment = 'projects'
        }
      })
      .catch(err => {
        setInitLoading(false)
        switch (err.message) {
          case 'user_not_logged': {
            if ($urlData.segment !== '' || $urlData.segment !== 'auth') {
              snackList.add({ text: 'Não esta logado ou não encontrado' })
              $urlData.segment = 'auth'
            }
            break
          }
          default: {
            console.error(err)
          }
        }
      })
  })
</script>

<style>
  main {
    background-color: #fff;
    position: relative;
    max-width: 375px;
    width: 100%;
    min-height: 100vh;
    margin: 0;
  }

  div {
    box-sizing: border-box;
  }

  div.header-section {
    background-color: inherit;
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    height: 248px;
    z-index: 2000;
  }

  div.header-section > div.bar {
    position: relative;
    /* height: 144px; */
    height: 128px;
    width: 100%;
  }

  div.header-section > div.search {
    display: flex;
    flex-direction: column;
    height: 104px;
    padding: 24px 16px;
    width: 100%;
  }

  div.header-section > div.bar > :global(*) {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
  }

  div.fixed-section {
    top: 0px;
    background-color: #fff;
    width: 100%;
    padding: 8px 16px;
    overflow: visible;
    display: grid;
    grid-template-columns: 72px 1fr;
    grid-column-gap: 16px;
    z-index: 1000;
  }

  /* div.fixed-section.elevation::before {
    content: '';
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    box-shadow: var(--elevation1);
  } */

  div.fixed-section > .modal,
  div.fixed-section > .card {
    position: relative;
    height: 100px;
  }

  div.content-section {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-areas: 'child';
    overflow-x: hidden;
    padding-bottom: 32px;
  }

  div.content-section > :global(*) {
    grid-area: child;
    overflow: hidden;
  }

  div.full-float > :global(*) {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100px;
  }
</style>

<main>
  <FetchProgress />
  <div class="header-section">
    <div class="bar">
      {#if typeof bar === 'string'}
        <DefaultBar title={bar} />
      {:else}
        <svelte:component this={bar} />
      {/if}
    </div>
    {#if !hideSearch}
      <div class="search">
        <Input label={searchTitle || 'Buscar...'} emphasis />
      </div>
    {/if}
  </div>
  <div>
    {#if !hideSearch}
      <div class="fixed-section">
        <div class="modal">
          <svelte:component this={modal} />
        </div>
        <div class="card full-float">
          <svelte:component this={component} />
        </div>
      </div>
    {/if}
    <div class="content-section">
      <svelte:component this={getPage($urlData.segment)} />
    </div>
  </div>
  <ModalContainer />
</main>
<SnackMessage />
