<script>
  import { fly } from 'svelte/transition'
  import { ripple } from 'fluido-components'

  // Stores
  import { urlData } from '../stores/router'
  import {
    currentProject,
    currentTemplate,
    currentField,
  } from '../stores/project'
  import { modalDataList } from '../stores/main'

  // Components
  import { crossfadeSend, crossfadeReceive } from '../components/utils'
  import ModalRemoveProject, * as ModalRemoveProjectData from '../components/ModalRemoveProject.svelte'
  import IconButton from '../components/IconButton.svelte'
  import PopMenu from '../components/PopMenu.svelte'

  let primaryColor = '#ffffff'
  let secondaryColor = '#121212'

  let self = null
  let title = ''
  let backButton = false

  $: {
    if ('field' in $urlData.query) {
      title = $currentField ? $currentField.name || '' : ''
      backButton = true
    } else if ('template' in $urlData.query) {
      title = $currentTemplate ? $currentTemplate.name || '' : ''
      backButton = true
    } else {
      title = $currentProject ? $currentProject.name || '' : ''
      backButton = false
    }
  }

  $: {
    primaryColor = $currentProject.primaryColor
      ? $currentProject.primaryColor
      : primaryColor
    secondaryColor = $currentProject.secondaryColor
      ? $currentProject.secondaryColor
      : secondaryColor
  }

  function callClose() {
    $urlData.segment = 'projects'
    $urlData.query = {}
  }

  function callBack() {
    if ('field' in $urlData.query) {
      delete $urlData.query.field
      $urlData.query = { ...$urlData.query }
    } else if ('template' in $urlData.query) {
      delete $urlData.query.template
      $urlData.query = { ...$urlData.query }
    }
  }

  let button
  let openMenu = false

  const popMenuList = [{ key: 'remove', label: 'Excluir Projeto' }]

  function handleMenuButton() {
    openMenu = !openMenu
  }

  function popMenuHandler(evt) {
    const item = evt.detail
    switch (item.key) {
      case 'remove': {
        modalDataList.add({
          content: ModalRemoveProject,
          ...ModalRemoveProjectData,
        })
        break
      }
    }
  }
</script>

<style>
  div.main {
    overflow: hidden;
    height: 100%;
    width: 100%;
    background-color: var(--bar-primary-color, #fff);
    color: var(--bar-secondary-color, var(--color-black));
    display: grid;
    grid-template-columns: 100%;
    grid-template-rows: 64px 64px;
    box-sizing: border-box;
  }

  div.main > * {
    flex: 1 0 auto;
    box-sizing: border-box;
  }

  div.main > div.top {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    padding: 0 8px;
  }

  div.main > div.top > * {
    flex: 0 0 auto;
  }

  div.main > div.top > h1 {
    flex: 0 1 216px;
    margin-right: auto;
    margin-left: 8px;
  }

  ul {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    padding: 0;
    margin: 0;
    width: 100%;
  }

  li {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    position: relative;
  }

  li > span {
    display: inline-block;
    width: 100%;
    text-align: center;
    margin: 16px 8px;
  }

  li > div {
    width: 100%;
    height: 0;
    border-bottom: 2px solid var(--bar-secondary-color, var(--color-black));
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
  }
</style>

<svelte:head>
  <meta name="theme-color" content={primaryColor} />
</svelte:head>

<div
  bind:this={self}
  transition:fly={{ y: -200, duration: 300 }}
  style="--bar-primary-color: {primaryColor}; --bar-secondary-color: {secondaryColor}"
  class="main">
  <div class="top">
    <IconButton
      icon={backButton ? 'arrow_back' : 'close'}
      on:click={ev => (backButton ? callBack(ev) : callClose(ev))} />
    <h1 class="type-h6">{title}</h1>
    <IconButton
      icon="more_vert"
      on:click={handleMenuButton}
      bind:node={button} />
  </div>
  <ul>
    {#each [['templates', 'Modelos'], ['users', 'Usuários']] as tab (tab[0])}
      <li use:ripple on:click={() => ($urlData.segment = tab[0])}>
        <span class="type-button">{tab[1]}</span>
        {#if $urlData.segment === tab[0]}
          <div
            out:crossfadeSend={{ key: 'tab_line' }}
            in:crossfadeReceive={{ key: 'tab_line' }} />
        {/if}
      </li>
    {/each}
  </ul>
  <PopMenu
    bind:open={openMenu}
    container={self}
    on:click={popMenuHandler}
    list={popMenuList}
    anchor={button} />
</div>
