<script>
  import { createEventDispatcher } from 'svelte'
  import { ripple } from 'fluido-components'
  import { scale } from 'svelte/transition'
  import { preventEvent } from './utils'

  export let node = null
  export let icon = 'add'

  let eventType = null

  const dispatch = createEventDispatcher()

  function handleClick(evt) {
    dispatch('click', { target: evt.target })
    return preventEvent(evt)
  }
</script>

<style>
  button {
    height: 48px;
    width: 48px;
    border: none;
    outline: none;
    border-radius: 1000000px;
    color: inherit;
    cursor: pointer;
    background-color: transparent;
  }

  button > div {
    display: grid;
    justify-content: center;
    align-items: center;
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
  }

  button:focus::after,
  button:hover::after {
    content: '';
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    border-radius: 1000000px;
    background-color: currentColor;
    opacity: 0.12;
  }

  i {
    grid-column: 1;
    grid-row: 1;
  }
</style>

<button
  use:ripple={{ center: true }}
  on:click={handleClick}
  bind:this={node}
  class="noselect">
  <div>
    {#each [icon] as ic (ic)}
      <i transition:scale class="material-icons">{ic}</i>
    {/each}
  </div>
</button>
