<script>
  import { onMount, onDestroy, createEventDispatcher } from 'svelte'
  import Popper from 'popper.js'
  import TinyColor from '@ctrl/tinycolor'
  import { preventEvent } from './utils'

  // Stores
  import { currentProject } from '../stores/project'

  $: primaryColor = $currentProject
    ? $currentProject.primaryColor || '#ffffff'
    : '#ffffff'
  $: secondaryColor = $currentProject
    ? $currentProject.secondaryColor || '#121212'
    : '#121212'
  $: light = new TinyColor(primaryColor).isLight()

  const dispatch = createEventDispatcher()

  export let list = []
  export let anchor = null
  export let open = true
  export let container = null

  let self = null
  let control = null

  $: if (!!anchor && !!self) {
    if (!!control) control.destroy()
    let config = { placement: 'bottom' }

    if (!!container)
      config.modifiers = {
        flip: {
          behavior: ['left', 'right'],
        },
        preventOverflow: {
          boundariesElement: container,
        },
      }
    control = new Popper(anchor, self, config)
  }

  $: if (open && !!control) {
    control.update()
  }

  function handleExternalClick(evt) {
    if (open) open = false
  }

  onMount(() => {
    window.addEventListener('click', handleExternalClick)
  })

  onDestroy(() => {
    window.removeEventListener('click', handleExternalClick)
  })
</script>

<style>
  ul {
    list-style: none;
    padding: 8px 0;
    margin: 0 -2px 0 16px;
    background-color: var(--snack-primary-color, #fff);
    color: var(--snack-secondary-color, var(--color-black));
    border: 2px solid currentColor;
    box-sizing: border-box;
    visibility: collapse;
    opacity: 0;
    transition: visibility 0.2s, opacity 0.2s;
  }

  li {
    padding: 0;
    position: relative;
    min-width: 72px;
  }

  li:hover > div {
    background-color: rgba(0, 0, 0, 0.12);
  }

  li > div {
    padding: 8px 16px;
  }

  hr {
    margin: 8px 0;
    border: none;
    border-bottom: 1px solid var(--color-black);
  }

  ul.open {
    visibility: visible;
    opacity: 1;
  }
</style>

<ul
  on:click={preventEvent}
  bind:this={self}
  style="--snack-primary-color: {primaryColor}; --snack-secondary-color: {secondaryColor};"
  class:light
  class:open
  class="noselect">
  {#each list as item (item.key)}
    <li on:click={() => dispatch('click', item)}>
      <div>{item.label}</div>
      {#if !!item.divider}
        <hr />
      {/if}
    </li>
  {/each}
</ul>
