<script>
  import { onMount } from 'svelte'
  import { fade } from 'svelte/transition'
  import Popper from 'popper.js'

  // Components
  import IconButton from '../components/IconButton.svelte'
  import Button from '../components/Button.svelte'
  import PopMenu from '../components/PopMenu.svelte'

  // Stores
  import { urlData } from '../stores/router'

  // Helpers
  import { signOut } from '../helpers/auth'

  export let title

  let button
  let pop
  let open = false
  let self = null

  function toggleMenu() {
    open = !open
  }

  const popMenuList = [{ key: 'logout', label: 'Sair' }]

  function popMenuHandler(evt) {
    const item = evt.detail
    switch (item.key) {
      case 'logout': {
        signOut()
        $urlData.segment = 'auth'
        break
      }
    }
  }
</script>

<style>
  div.app-bar {
    position: relative;
    display: flex;
    flex-direction: column-reverse;
    background-color: #fff;
  }

  div.logout div.top {
    display: none;
  }

  div.top {
    position: absolute;
    top: 8px;
    right: 8px;
    z-index: 0;
  }

  h1 {
    text-align: center;
    opacity: 0.7;
  }
</style>

<div
  bind:this={self}
  transition:fade={{ duration: 300 }}
  class="app-bar"
  class:logout={$urlData.segment === 'auth'}>
  <h1 class="type-h3">{title}</h1>
  <div class="top">
    <IconButton bind:node={button} icon="more_vert" on:click={toggleMenu} />
  </div>
  <PopMenu
    bind:open
    container={self}
    on:click={popMenuHandler}
    list={popMenuList}
    anchor={button} />
</div>
