<script>
  import { primitives } from '../stores/main'
  import { ripple } from 'fluido-components'

  export let extension = null
  export let extensionType = null

  let embed = {
    label: 'Incluir Modelo',
    icon: 'library_books',
    name: 'Template',
  }
  let indexSelect = 0

  $: {
    const pri = $primitives[indexSelect]
    if (pri) {
      extension = pri._id
      extensionType = 'primitive'
    } else {
      extension = null
      extensionType = 'template'
    }
  }

  function setPrimitive(index) {
    if (index === indexSelect) indexSelect = 0
    else indexSelect = index
  }
</script>

<style>
  div.primitives {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(48px, 1fr));
    justify-content: space-around;
  }

  div.primitives > div {
    display: flex;
    justify-content: center;
    position: relative;
  }

  div.primitives > div > i {
    width: 40px;
    height: 40px;
    line-height: 40px;
    border-radius: 1000000px;
    text-align: center;
    opacity: 0.3;
    outline: none;
  }

  div.primitives > div.select > i {
    opacity: 1;
  }

  div.primitives > div:focus::before,
  div.primitives > div:hover::before {
    content: '';
    position: absolute;
    top: 0;
    bottom: 0;
    left: calc(50% - 20px);
    width: 40px;
    border-radius: 1000000px;
    background-color: currentColor;
    opacity: 0.12;
  }

  span.title {
    text-align: center;
    line-height: 56px;
  }
</style>

<span class="type-subtitle2 title">Tipo de campo</span>
<div class="primitives">
  {#each [...$primitives] as pri, index (pri.name)}
    <div class:select={indexSelect === index}>
      <i
        tabindex="0"
        use:ripple={{ center: true }}
        title={pri.label}
        class="material-icons"
        on:click={() => setPrimitive(index)}>
        {pri.icon || 'not_interested'}
      </i>
    </div>
  {/each}
</div>
