<script>
  // Helpers
  import { slugifyConfigured } from '../helpers'

  // Components
  import Input from './Input.svelte'
  import Checkbox from './Checkbox.svelte'
  import AddModal from './AddModal.svelte'
  import Button from './Button.svelte'
  import PrimitivesSelection from './PrimitivesSelection.svelte'

  // Stores
  import { urlData } from '../stores/router'
  import { listTemplates, listFields } from '../stores/project'

  // API
  import { addTemplate } from '../api/template'
  import { addField } from '../api/field'

  let name = ''
  let slug = ''
  let field = {}

  let focusName = false
  let close

  function clear() {
    name = ''
    slug = ''
    field = {}
  }

  let inputNode

  function callOpen({ detail }) {
    if (detail && inputNode) {
      inputNode.focus()
      inputNode.select()
    }
    if (!detail) clear()
  }

  function slugDigest(text) {
    let s = slugifyConfigured(text)

    if (text.trim() !== s) {
      return s
    }
    return text
  }

  $: if (focusName) slug = name

  $: third = 'template' in $urlData.query && 'field' in $urlData.query
  $: second = 'template' in $urlData.query && !('field' in $urlData.query)
  $: first = !('template' in $urlData.query)
  $: if (first) field = {}

  function saveAction() {
    let toSend = {
      name,
      slug,
      ...field,
    }
    if (close) close()
    setTimeout(() => {
      if (first) {
        toSend.order = $listTemplates.length
        addTemplate(toSend).then(() => {
          clear()
        })
      } else {
        toSend.order = $listFields.length
        console.log(toSend)
        addField(toSend).then(() => {
          clear()
        })
      }
    }, 500)
  }
</script>

<style>
  div.template-form {
    width: 100%;
    display: grid;
    padding: 8px;
    grid-template-columns: repeat(2, minmax(0, 1fr));
    grid-gap: 8px;
    justify-content: center;
    align-content: flex-start;
  }

  div.template-form .fullWidth {
    grid-column-start: 1;
    grid-column-end: 3;
    display: flex;
    flex-direction: column;
  }

  div.template-actions {
    display: flex;
    width: 100%;
    flex-direction: column;
    justify-content: stretch;
    padding: 16px 32px;
  }
</style>

<AddModal
  title={`Novo ${first ? 'Modelo' : 'Campo'}`}
  on:open={callOpen}
  bind:close>
  <div class="template-form">
    <div class="fullWidth">
      <Input
        label={`Nome do ${first ? 'Modelo' : 'Campo'}`}
        bind:node={inputNode}
        bind:value={name}
        on:input={() => (focusName = true)}
        on:blur={() => (focusName = false)} />
    </div>
    <div class="fullWidth">
      <Input
        bind:value={slug}
        digest={slugDigest}
        label={`Slug do ${first ? 'Modelo' : 'Campo'}`} />
    </div>
    {#if second}
      <div class="fullWidth">
        <PrimitivesSelection
          bind:extension={field.extension}
          bind:extensionType={field.extensionType} />
      </div>
      <div class="fullWidth">
        <Checkbox label="Deve ser obrigatório" bind:value={field.isRequired} />
      </div>
    {/if}
  </div>
  <div class="template-actions">
    <Button on:click={saveAction}>criar {first ? 'modelo' : 'campo'}</Button>
  </div>
</AddModal>
