  let canRemove = false

  export const title = 'Excluir Modelo'
  export const actions = [
    {
      label: 'Sim',
      order: -1,
      callback: () => {
        if (canRemove) removeTemplate(canRemove.slug)
        return true
      },
    },
  ]
</script>

<script>
  export let template

  $: if (!!template) canRemove = template
  else canRemove = false
</script>

