<script>
  import { fade } from 'svelte/transition'

  // Components
  import { preventEvent } from './utils'
  import IconButton from './IconButton.svelte'
  import Button from './Button.svelte'

  // Stores
  import { modalDataList } from '../stores/main'

  function exitModal(modal) {
    modalDataList.remove(modal.__id)
  }
</script>

<style>
  div.modal-wrapper {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.7);
    z-index: 5000;
  }

  div.modal {
    position: absolute;
    top: 64px;
    left: 50%;
    transform: translateX(-50%);
    min-width: 352px;
    max-width: 720px;
    background-color: #fff;
    border: 2px solid var(--color-black);
  }

  div.modal-title {
    display: grid;
    grid-template-columns: 1fr 48px;
    align-items: center;
    height: 48px;
  }

  h3 {
    margin-left: 16px;
  }

  div.modal-content {
    padding: 16px;
  }

  div.modal-actions {
    padding: 16px;
    display: flex;
    flex-wrap: nowrap;
    justify-content: flex-end;
  }

  div.modal-actions > :global(*) {
    margin-left: 8px;
  }
</style>

{#each $modalDataList as modal, index}
  <div
    class="modal-wrapper"
    on:click={() => exitModal(modal)}
    transition:fade={{ duration: 200 }}>
    <div class="modal" on:click={preventEvent}>
      <div class="modal-title">
        <h3 class="type-h5">{modal.title || 'Título'}</h3>
        <IconButton icon="close" on:click={() => exitModal(modal)} />
      </div>
      <div class="modal-content type-body">
        {#if typeof modal.content === 'string'}
          {modal.content}
        {:else}
          <svelte:component this={modal.content} {...modal.properties || {}} />
        {/if}
      </div>
      <div class="modal-actions">
        <Button on:click={() => exitModal(modal)}>Cancelar</Button>
        {#each modal.actions as button}
          <Button
            style="order: {button.order || 0}"
            on:click={() => {
              if (button.callback) {
                if (button.callback(index)) exitModal(modal)
              } else {
                exitModal(modal)
              }
            }}>
            {button.label}
          </Button>
        {/each}
      </div>
    </div>
  </div>
{/each}
