<script context="module">
  import TemplatesAppBar from './_TemplatesBar.svelte'
  import TemplatesAppCard from './_TemplatesCard.svelte'
  import AddTemplateModal from '../components/AddTemplateModal.svelte'

  export let searchTitle = 'Buscar...'
  export let bar = TemplatesAppBar
  export let modal = AddTemplateModal
  export let component = TemplatesAppCard
</script>

<script>
  import { onMount, onDestroy } from 'svelte'

  // Motion
  import anime from 'animejs'
  import { fly } from 'svelte/transition'

  // Stores
  import { urlData } from '../stores/router'
  import { modalDataList } from '../stores/main'
  import {
    listTemplates,
    listFields,
    recentProjectSlug,
  } from '../stores/project'

  // Components
  import SwipeView from '../components/SwipeView.svelte'
  import DragList from '../components/DragList.svelte'
  import ListItem from '../components/ListItem.svelte'
  import IconButton from '../components/IconButton.svelte'
  import PopMenu from '../components/PopMenu.svelte'
  import ModalRemoveTemplate, * as ModalRemoveTemplateData from '../components/ModalRemoveTemplate.svelte'

  let index = 0
  let self = null

  $: third = 'field' in $urlData.query
  $: second = 'template' in $urlData.query && !third
  $: first = !second && !third

  $: switch (true) {
    case third: {
      index = 2
      break
    }
    case second: {
      index = 1
      break
    }
    case first:
    default: {
      index = 0
    }
  }

  let popMenuList = []
  let popMenuAnchor = null
  let popMenuOpen = false

  function actionTemplateItemhHandler(evt, template) {
    const { action, node } = evt.detail

    switch (action) {
      case 'more_vert': {
        popMenuList = [{ label: 'Editar', key: 'edit' }]
        if (node === popMenuAnchor && popMenuOpen) {
          popMenuOpen = false
        } else {
          popMenuAnchor = node
          popMenuOpen = true
        }
        break
      }
      case 'delete': {
        modalDataList.add({
          ...ModalRemoveTemplateData,
          content: ModalRemoveTemplate,
          properties: { template },
        })
        break
      }
    }
  }

  function selectTemplate(template) {
    $urlData.query.template = template.slug
    listFields.set(template.fields)
  }

  onMount(() => {
    window.dispatchEvent(new CustomEvent('reload-template-list'))
    window.scrollTo(0, 0)
    if ($urlData.query)
      setTimeout(() => {
        recentProjectSlug.set($urlData.query.project)
      }, 500)
  })

  onDestroy(() => {
    listTemplates.set([])
  })
</script>

<style>
  section {
    overflow: hidden;
    width: 100%;
  }

  div {
    padding: 16px 8px;
  }
</style>

<section transition:fly={{ x: 300 }} bind:this={self}>

  <SwipeView {index}>
    <div>
      <DragList bind:items={$listTemplates} let:item={template} key="slug">
        <ListItem
          actions={['delete', 'more_vert']}
          on:click={() => selectTemplate(template)}
          on:action-click={evt => {
            actionTemplateItemhHandler(evt, template)
          }}>
          {template.name}
        </ListItem>
      </DragList>
    </div>
    <div>
      <DragList bind:items={$listFields} let:item={field} key="slug">
        <ListItem
          actions={['delete', 'more_vert']}
          on:click={() => ($urlData.query.field = field.slug)}>
          {field.name}
        </ListItem>
      </DragList>
    </div>
    <div>C</div>
  </SwipeView>
  <PopMenu
    bind:container={self}
    bind:list={popMenuList}
    anchor={popMenuAnchor}
    bind:open={popMenuOpen} />
</section>
